/*
 * Main font
 * 
 */

@import (css) url("//fonts.googleapis.com/css?family=Open+Sans+Condensed:300|Open+Sans:400italic,400,300,600");


/*
 * Icons
 * 
 */

@font-face {
  font-family: 'fontello';
  src: url('@{fontPath}/fontello.eot?84544658');
  src: url('@{fontPath}/fontello.eot?84544658#iefix') format('embedded-opentype'),
       url('@{fontPath}/fontello.woff?84544658') format('woff'),
       url('@{fontPath}/fontello.ttf?84544658') format('truetype'),
       url('@{fontPath}/fontello.svg?84544658#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'fontello';
    src: url('@{fontPath}/fontello.svg?84544658#fontello') format('svg');
  }
}
*/
 
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
 
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
 
.icon-spin5:before { content: '\e800'; } /* '' */
